@media(max-width: 640px) {

  a#logo {
    top: 40px;
  }
}


@media(max-width: 540px) {
  // invisible -----------------------------------------------------------------

  #preview {
    display: none;
  }

  // grille --------------------------------------------------------------------
  #buying,
  #wycena_nag,
  #wycena_suma,
  #dodaj_koszyk {
    @include span(12);
    padding-left: 0;
    padding-right: 0;
  }

  #header {
    height:300px;
    background-image: url('../images/mobile480b.png');
    width: 100%;
    background-size: contain;
  }


  //----------------------------------------------------------------------------
  //                             MODULES
  //----------------------------------------------------------------------------

  // Header --------------------------------------------------------------------

  button#responsive-menu-button {
    top: 10px;
    right: 10px;
    margin: 0;
  }

  // module logo
  a#logo {
    top: 80px;
    left: 10px;
    width: 22%;
    height: 200px;
  }

  // module titre site
  .site-head{
    top: 120px;
    left: 24%;
    width: 71%;
    .site-title{
      font-size: 2.2rem;
      line-height: 2.2rem;
    }
    .site-subtitle{
      padding: 0 4px;
      font-size: .8rem;
      line-height: .85rem;
    }
  }

  // module accès client
  ul.menu-client{
    font-size: .7rem;
    line-height: 1.1rem;
    top:150px;
    height:80px;
    li{
      height: 60px;
    }
   .menu-client-icon {
      display: block;
      font-size: 3rem;
      color: $cyan;
    }
    .menu-client-icon.phone {
      padding-right: 10px;
      padding-top: 10px;
    }
    .menu-client-phone{
      padding-left: 10px;
    }
    .tel1{
      font-size: 1rem;
    }
    .tel2 {
      font-size: 1.3rem;
    }
    .tel3 {
      font-size: .7rem;
      text-align: right;
    }
  }



  .bloc-left,
  .bloc-right{
    width: 100%;
    float: none;
    margin: 0;
  }
  //----------------------------------------------------------------------------
  //                      MODULES PAGE ACCUEIL
  //----------------------------------------------------------------------------

  // module icones tarifs
  .b-it1{
    margin-right: 2%;
  }
  .b-it2 {
    margin-right: 0;
  }
  .b-it3 {
    margin-right: 2%;
  }
  ul#tarifs {
    li {
      width: 48%; //120
    }
  }



  //----------------------------------------------------------------------------
  //                      MODULES PAGES PRODUITS
  //----------------------------------------------------------------------------





  // tableau produit commandé détail -------------------------------------------
  .disMob0 {
    display: inline;
    font-weight: bold;
    text-transform: uppercase;
    color: $cyan-bloc;
  }


  #fbcart_cart {
    position: relative;
    float: left;
    display: inline-block;
    @include container(100%);
    width: 100%;
    span {
      &.therest {
        display: none;
      }
    }

    th {
      display: none;
    }

    td {
      display: inline-block;
      width: 100%;
      padding: .5rem;
      background-image: none;

      &.lefttd {
        width: 100%;
      }
      &.noprint{
        width: 5%;
      }
      &.tdtotal{

      }
    }


  }
  #comment_left{
    padding: 1rem;
  }
}
